import {
    fadeIn,
    fadeOut
} from "../services/constFunctions";

function pageUp(trigger) {
    const pageUp = document.querySelector(trigger);

    window.addEventListener('scroll', () => {

        if (scrollY >= 800) {
            fadeIn(pageUp, 1000, 0.7);
        } else {
            fadeOut(pageUp, 1000, 0);
        }

    });
}

export default pageUp;